/* Small Screen */
.container {
    background-color: #43eee0;
    padding: 1rem;
    color: #fff;
    }
  
  .footer {
    display: flex;
    flex-direction: column;
    }
  
  .footer__brand {
    font-size: 2.4rem;
    margin-bottom: 1rem;
    height: 59px;
    width: 163px;
    left: 122px;
    top: 3004px;
    border-radius: nullpx;

    }
  
  .footer__list {
    display: flex;
    flex-direction: column;
    list-style: none;
    } 
  
  .footer__item {
    margin-bottom: 1rem;
    }
  
  /* Medium Screen */
  @media (min-width: 768px) {
    .footer {
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
    }
  
    .footer__brand {
      margin-bottom: 0;
    }
  
    .footer__list {
      flex-direction: row;
    }
  
    .footer__item {
      margin: 0 1rem;
    }
  }
  
  /* Large Screen */
  @media (min-width: 992px) {

  }
  